@import "~reset-css";
@import "~antd/dist/antd.css";
@import "layouts/chat";

body {
  background-color: #f4f7fd;
}

body,
html,
#root,
.wrapper {
  height: 100%;
  color: #202020;
  font-family: "Tahoma,sans-serif,Arial,Helvetica";
  font-size: 14px;
  font-display: swap;
}
p {
  font-weight: 550;
}
* {
  outline: none;
}

.ant-btn-circle {
  border: 0;
  box-shadow: none;
  color: #202020 !important;
  opacity: 0.4;

  &:hover {
    opacity: 1;
  }

  &:after {
    display: none;
  }
}