  
.avatar {
    border-radius: 50%;
    width: 100%;
    height: 100%;
    cursor: pointer;
    &--symbol {
      color: #fff;
      justify-content: center;
      align-items: center;
      font-weight: 600;
      display: flex;
      line-height: 38px;
      font-size: 18px;
    }
  }